<template>
	<el-form :rules="rules" ref="form" style="width: 400px" :model="form" label-width="100px">
	  <el-form-item label="" prop="name">
	    <el-input v-model="form.name"></el-input>
	  </el-form-item>
	 	<el-form-item label="" prop="capital_account">
	    <el-input v-model="form.capital_account"></el-input>
	  </el-form-item>
	  <el-form-item label="" prop="phone">
	    <el-input v-model.number="form.phone"></el-input>
	  </el-form-item>
	  <el-form-item label="">
	    <el-select v-model="form.status" placeholder="">
	    <el-option
	      v-for="item in options"
	      :key="item.value"
	      :label="item.label"
	      :value="item.value">
	    </el-option>
  </el-select>
	  </el-form-item>
	  <el-form-item label=" ">
	    <el-button type="primary" @click="submitForm('form')"></el-button>
    	<el-button @click="resetForm()"></el-button>
	  </el-form-item>
  </el-form>
</template>

<script>
import {ASYNC_INSERT_CUSTOMER} from "./customerStore.js"
import {mapState, mapActions, mapMutations } from "vuex"
export default {
	data() {
			return {
				form: {
					name: "",
					capital_account: "",
					phone: "",
					status: "0"
				},
				options: [{
					value: '0',
					label: ''
				}, {
					value: '1',
					label: ''
				}],
				flag: 0,
				rules: {
          name: [
            { required: true, message: '', trigger: 'blur' },
          ], 
          capital_account: [
            { required: true, message: '', trigger: 'blur' },
          ],
          phone: [
          	{ required: true, message: ''},
          ]
        }
			}
		},
		methods: {
			async submitForm(formName) {
				this.$refs[formName].validate(async (valid) => {
					if (valid) {
						if (this.flag != 0) {
							return this.$message.error('!!');
						}
						this.flag = 1
						const data = await this[ASYNC_INSERT_CUSTOMER](this.form)
						if (data._id) {
							this.$message('!');
						}
					} else {
						return false;
					}
				});
			},
			resetForm() {
				Object.assign(this.form, {
					name: "",
					capital_account: "",
					phone: ""
				})
				this.flag = 0
			},
	    ...mapActions("customer", [ASYNC_INSERT_CUSTOMER]),
		}
}
</script>
<style></style>


